import React, { Component } from 'react';
import Icons from '../Icons/Icons';
import ApplyForm from '../applyForm';
import './style.css';




export default class AKDropDown extends Component {
  constructor(props) {
    super(props);  
    this.state = {
      active :false, 
      agree :false,
      index:0,
      tab_visible :false,    
    };
  }
  componentDidMount() {  
    const _th = this;
    setTimeout(()=>{
      _th.setState({active:true});
    },250);
  }  
  componentWillUnmount(){
    
  }

  OpenTabView(){
    const {tab_visible} = this.state;
    this.setState({tab_visible:!tab_visible});
  }


  handleSetItem(i){
    const {tab_visible} = this.state;
    this.setState({tab_visible:!tab_visible,index:i+1});
  }

  ref = r => {
    this.MS_Elem = r
  }
  render() { 
     const {title,list} = this.props;
     const {index} = this.state;
      return (                         
              <div className={`drop-box-container mqn2-be2 ${this.state.active?"active":""} ${this.state.tab_visible?'dp-open':''}`}>
                <div className="drop-box-header" onClick={this.OpenTabView.bind(this)}>
                  <div className="leftIcon">      
                    <div className="material-icons"><Icons name={'ckeck-circle'} className="material-icons" color={'#4285f4'} size={24}/></div>                               
                    <div className="mqn2-bdu">{list.lenght}</div>
                  </div>
                  <div className="center-label">
                    <span>{index>0?list[index-1]:title}</span>                  
                  </div>
                  <div className="status-Icon">
                    <div className="material-icons"><Icons name={'arrow_right'} className="material-icons" color={'#7e7e7e'} size={24}/></div>
                  </div>
                </div>
                <div className="dp-open--box">
                  {list.map((s,i)=>{
                    return(
                      <div key={i} className="info--wrapper ak--dp--items" mqn-inview-fade-in=""  onClick={()=>this.handleSetItem(i)}>
                        <span className="info">  
                        {s}                    
                        </span>  
                      </div>     
                    )
                  })
                  }                         
                </div>
              </div>
      ) 
  }
}
